import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { removeFromCart, clearCart } from '../store/cartSlice';
import { Link } from 'react-router-dom';
import { Button } from '../components/Button';

const Container = styled.main`padding:2rem;`;
const Item      = styled.div`display:flex;align-items:center;gap:1rem;margin-bottom:1rem;`;
const Img       = styled.img`width:80px;height:60px;object-fit:cover;border-radius:4px;`;
const Info      = styled.div`flex:1;`;
const Total     = styled.h2`text-align:right;margin-top:2rem;`;

export default function Cart() {
  const items    = useSelector(state => state.cart.items);
  const dispatch = useDispatch();
  const total    = items.reduce((sum, i) => sum + i.preco * i.quantity, 0);

  if (!items.length) {
    return (
      <Container>
        <p>Seu carrinho está vazio.</p>
        <Link to="/">Voltar ao início</Link>
      </Container>
    );
  }

  return (
    <Container>
      <h1>Meu Carrinho</h1>
      {items.map(i => (
        <Item key={i.id}>
          <Img src={i.foto} alt={i.nome} />
          <Info>
            <h3>{i.nome}</h3>
            <p>Preço: R$ {i.preco.toFixed(2)} x {i.quantity}</p>
          </Info>
          <Button onClick={() => dispatch(removeFromCart(i.id))}>Remover</Button>
        </Item>
      ))}
      <Total>Total: R$ {total.toFixed(2)}</Total>
      <Button onClick={() => dispatch(clearCart())}>Limpar Carrinho</Button>
    </Container>
  );
}
